.App {
  /* gradient fading from blue at top to black at bottom */
  /*background: linear-gradient(to bottom, rgb(218, 131, 17), rgb(190, 10, 181));
  */
  background: #3F292B;
  text-align: center;
  position: relative;
  width: 100%;
  color: white;
  min-height: 100vh;
}

.social-buttons {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 15px;
  z-index: 1000;
}

.social-btn {
  display: inline-block;
  padding: 12px 24px;
  background-color: #003844;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid #61988E;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
}

.social-btn:hover {
  background-color: #61988E;
  color: #003844;
  box-shadow: 0 4px 8px rgba(97, 218, 251, 0.3);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* .App-header isnâ€™t used in the current markup, so styles were moved to .App */
.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

h1 {
  text-align: center;
}

/* remove default top margin on first heading to avoid body showing through */
h1 {
  margin-top: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
